.toggle-complete {
    width: 30px;
    height: 23px;
    position: absolute;
    margin-right: 8px;
    right: 20%;
}
.delete {
    background-color: rgb(238, 16, 16);
    position: absolute;
    height: 23px;
    width: 30px;
    right: 15%;
}
li {
    font-size: 18px;
    font-family: Georgia, 'Times New Roman', Times, sans-serif;
    list-style: none;
    width: 68%;
    height: 25px;
    border: 2px solid rgb(204, 197, 197);
    margin-bottom: 20px;
    padding-left: 5px;
    padding-top: 3px;
}
li.completed {
    background-color: rgb(60, 218, 60);  
}

li.completed span {
    color: rgb(4, 240, 248);
    color: aliceblue;
    overflow: hidden;
}
.completed .fa-check {
    color: rgb(56, 15, 204);
    font-weight: bold;
    font-size: large;
}
.completed{
    background-color: rgb(60, 218, 60);
}
